@import "./footer.css";

:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.6;
  font-weight: 400;
  color-scheme: light dark;
  color: #ffffffde;
  background-color: oklch(0.19 0 0);

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

a {
  text-decoration: inherit;
  color: inherit;
  transition: 0.3s all;
  padding: 0.6rem 1rem;
}

a:hover {
  filter: brightness(0.5);
}

body {
  margin: 0;
  place-items: center;
  min-width: 290px;
  min-height: 100vh;
}

nav {
  background: #000000;
  display: inline-block;
  border-bottom: 1px solid #4b4b4b;
  width: 100vw;
}

ul {
  list-style-type: none;
  display: flex;
  width: 100vw;
  padding-inline: 1rem;
  box-sizing: border-box;
  margin: 0;
}

li {
  font-size: 0.8em;
  font-weight: 400;
  transition: 0.3s all;
  display: contents;
}

h1 {
  line-height: 1.1;
  font-size: clamp(1.5rem, 5vw, 2.5rem);
  color: oklch(0.96 0.03 348.78);
  letter-spacing: -0.8px;
  margin: 0;
}

#app {
  margin: 0 auto;
  padding: 0;
  text-align: center;
}

.logo {
  height: 6em;
  padding: 1.5em;
  will-change: filter;
  transition: filter 300ms;
}
.logo:hover {
  filter: drop-shadow(0 0 2em #646cffaa);
}
.logo.vanilla:hover {
  filter: drop-shadow(0 0 2em #3178c6aa);
}

.read-the-docs {
  color: #888;
  font-size: 0.8rem;
}

button {
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 0.9em;
  font-weight: 500;
  font-family: inherit;
  background-color: #252525;
  cursor: pointer;
  transition: filter 0.3s;
  place-self: center;
}

button:hover {
  filter: brightness(0.8);
}

button:focus,
button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }

  button {
    background-color: #f9f9f9;
  }

  h1 {
    color: #213546;
  }
}

p {
  color: oklch(0.59 0 0);
  font-size: clamp(0.8rem, 2.1vw, 0.9rem);
  max-width: 80vw;
  margin-inline: auto;
  margin-bottom: 2em;
}

@media (min-width: 600px) {
  .logo {
    height: 8em;
    border-top: 1px solid rgba(255, 255, 255, 0.05);
  }

  button {
    padding: 0.8em 1.4em;
    font-size: 0.8em;
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  }

  .hero {
  }
}

.border-bottom {
  border-bottom: 1px solid #4b4b4b;
}

section {
  display: grid;
  place-content: center;
  max-height: 60vh;
  width: 100vw;
  aspect-ratio: 2 / 1;
  min-height: 350px;
}

img {
  max-width: 80vw;
  height: clamp(63px, 19vw, 100px);
  place-self: center;
  border-radius: 0.5rem;
}

::selection {
  background-color: #646cff;
  color: #ffffff;
}
button#counter {
  position: relative;
}
button#counter::after {
  content: "";
  background-color: #646cff;
  width: 2rem;
  height: 0.1em;
  bottom: 0;
  position: absolute;
  left: 50%;
  translate: -50% 100%;
  scale: 1.2;
  transition: width 0.3s;
}

button#counter:active {
  outline: none;
}

button:focus,
a:focus {
  outline: 1px solid rgb(102 110 255 / 50%);
}

button#counter:active::after {
  width: 3.5rem;
}

.hero {
  background: linear-gradient(45deg, black, transparent);
}
